@import url('https://fonts.googleapis.com/css?family=Press+Start+2P');
@import url('https://fonts.googleapis.com/css?family=RVT323');
@import './font.css';


.App {
  text-align: center;
  font-family: 'M PLUS 1 Code', monospace;
  color: white;
}

* {
  box-sizing: border-box;
}


.overlay {
  /* opacity: 0.85; */
  width: 100%;
  height: 100%;
  z-index: 10;
  top: 0;
  left: 0;
  position: absolute;  
}

.moving-background {
  background-position: center center;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-image: url("./assets/background/parallax-bg.gif");
  height: 870px;
}

@media screen and (max-width: 768px) {
  .moving-background {
     height: 720px; /* Cambia este valor seg√∫n sea necesario para pantallas medianas */
  }
 }

.content {
  background-color: #131301;
}

.welcome {
  font-family: 'Otomanopee One', sans-serif;
}
